<template>
   <!-- 모달창만들기 -->
     <div class="black-bg" v-if="모달창열렸니==true">
        <div class="white-bg">
          <img :src="제주여행지[제목누른거].image" style="width:100%">
          <h4>{{제주여행지[제목누른거].title}}</h4>
          <p>{{제주여행지[제목누른거].content}}</p>
           <p>{{제주여행지[제목누른거].price.toLocaleString()}}원</p>
             <!-- @input @click이랑 같은 거 -->
           <!-- <input @input="month=$event.target.value"> -->
           <input v-model.number="priceNumber">
           <p>{{priceNumber}}개를 선택함 :{{제주여행지[제목누른거].price * priceNumber}}원</p>
           <!-- read only기능 -->
          <button @click ="$emit('closeModal')">닫기버튼</button>
        </div>
     </div>
</template>

<script>
export default {
  name:"Modal",
  data(){
    return{
      priceNumber:0
    }
   },
   watch:{
    priceNumber(a){  //프라이넘버데이터가 바뀔 때 마다
    //if()만약에 10개 이상 구매하면 11개 이상 예약 못합니다라는 문구를 출력
        if(a > 11){
          alert("11개 이상은 선택할 수 없어요!!!!!")
        }
        //문자방지코드 문자입력시==> 문자입력하지마세요 
        //이후에 값을 1로 되돌림
        if(isNaN(a)==true){
          alert("문자입력하지마세요");
          this.priceNumber=1;
        }

    }

   }, 
   props:{
    제주여행지:Array,   //부모님이 가지고 있는 데이터의 원형===> 데이터이름:자료형
    제목누른거:Number,
    모달창열렸니:Boolean,
  }
    
  }



 


</script>

